<!-- 首页 -->
<template>
  <div class="home">
    <ul class="homebox">
      <li v-for="(item, i) in images" :key="i">
        <div class="imgbox">
          <img :src="item.img" alt="" :title="item.name" />
          <div class="imghover">
            <h3>{{ item.name }}</h3>
            <span class="remarks">{{ item.remarks }}</span>
            <div class="time">
              <span>{{ item.time }}</span>
            </div>
          </div>
        </div>
      </li>
      <li>
        <el-upload class="upload-demo imgbox" ref="uploadfiles" drag :action="' '" :auto-upload="false" :show-file-list="false" :on-change="imgChange" :limit="1" title="上传图片">
          <i class="el-icon-plus"></i>
        </el-upload>
      </li>
    </ul>

    <el-dialog :visible.sync="dialogVisible" @close="imgClose" class="uploadimgdialog">
      <img width="50%" :src="imageList.url" alt="" class="uploadimg" />
      <div class="formDataBox">
        <el-form :label-position="labelPosition" label-width="80px" :model="imageList">
          <el-form-item label="名称">
            <el-input v-model="imageList.name"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="imageList.remarks"></el-input>
          </el-form-item>
          <el-form-item label="上传时间">
            <el-date-picker v-model="imageList.time" type="datetime" placeholder="选择日期时间" style="width: 100%" format="yyyy-MM-dd HH:mm"> </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button @click="imgClose">取 消</el-button>
            <el-button type="primary" @click="imguploadRequest">确 定</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  name: 'home',
  //import引入的组件需要注入到对象中才能使用
  components: {},
  props: {},
  data() {
    //这里存放数据
    return {
      images: [], // 图片数据
      imageList: {
        name: '',
        img: '',
        url: '',
        time: new Date(),
        remarks: '',
        list: {}
      }, // 上传图片信息
      dialogVisible: false, // 上传图片弹窗
      labelPosition: 'right'
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    // 获取图片数据
    async queryData() {
      this.images = [];
      let list = await this.axios.get('/echarts/queryImageData');
      console.log(list, 'ttt');
      list.forEach(item => {
        console.log(item.img.data.toString());
        const bufferUrl = btoa(new Uint8Array(item.img.data).reduce((data, byte) => data + String.fromCharCode(byte), ''));
        this.images.push({img: 'data:image/png;base64,' + bufferUrl, name: item.name, time: item.time, remarks: item.remarks});
      });
    },

    // 上传图片
    imgChange(file) {
      // this.imageList.name = file.name;
      this.imageList.img = file.raw;
      this.imageList.url = URL.createObjectURL(file.raw);
      this.imageList.list = file;
      this.dialogVisible = true;
    },

    // 上传图片到服务器
    imguploadRequest() {
      const formData = new FormData();
      formData.append('name', this.imageList.name);
      formData.append('file', this.imageList.img);
      formData.append('time', this.dateformatter(this.imageList.time, 0));
      formData.append('remarks', this.imageList.remarks);
      this.axios
        .post('/echarts/imgUpload', formData, {headers: {'content-type': 'multipart/form-data'}})
        // .post('/echarts/imgUpload', {name: this.imageList.name, img: this.imageList.list, time: this.imageList.time, remarks: this.imageList.remarks})
        .then(res => {
          console.log(res, 'res');
          this.dialogVisible = false;
          this.queryData();
        })
        .catch(err => {
          console.log(err, 'err');
        });
    },

    // 图片信息弹窗关闭
    imgClose() {
      this.dialogVisible = false;
      this.$refs.uploadfiles.clearFiles();
    },

    // 时间格式
    dateformatter(date, num) {
      date = new Date(date.getTime() + 24 * 60 * 60 * 1000 * num);
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      var d = date.getDate();
      var hh = date.getHours();
      // var mm = date.getMinutes();
      // var ss = date.getSeconds();
      return y + '-' + (m < 10 ? '0' + m : m) + '-' + (d < 10 ? '0' + d : d) + ' ' + (hh < 10 ? '0' + hh : hh) + ':00'; //+ (mm < 10 ? '0' + mm : mm) + ':' + (ss < 10 ? '0' + ss : ss);
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.queryData();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang="scss" scoped>
.home {
  width: 100%;
  height: 100%;
  .homebox {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;
    overflow-y: auto;
    li {
      width: calc(100% / 5);
      height: calc(100% / 3);
      margin: 20px 0;
      padding: 0 20px;
      // background-color: #ccc;
      box-sizing: border-box;
      .imgbox {
        position: relative;
        width: 100%;
        height: 100%;
        background-color: #fff;
        border-radius: 7px;
        box-shadow: 0 0 20px #ccc;
        cursor: pointer;
        img {
          width: 100%;
          height: 100%;
        }
        .imghover {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          padding: 10px;
          background-color: rgba(255, 255, 255, 0.7);
          opacity: 0;
          box-sizing: border-box;
          h3 {
            font-size: 24px;
            font-weight: 400;
          }
          .time {
            position: absolute;
            bottom: 10px;
          }
        }
        &:hover {
          .imghover {
            opacity: 1;
          }
        }
      }

      /deep/ .upload-demo {
        .el-upload {
          width: 100%;
          height: 100%;
          .el-upload-dragger {
            width: 100%;
            height: 100%;
            border: 0;
            .el-icon-plus {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              font-size: 100px;
              color: #ccc;
            }
          }
        }
      }
    }
  }
  .uploadimgdialog {
    font-size: 0;
    .uploadimg {
      margin-right: 10%;
      border: 1px solid #ccc;
      border-radius: 7px;
      box-sizing: border-box;
    }
    .formDataBox {
      display: inline-block;
      width: 30%;
      margin-top: 12%;
      vertical-align: top;
    }
  }
}
</style>
